import React, { useEffect, useState } from "react";
import "./collapsibleSettings.scss";
import CheckIcon from "@mui/icons-material/Check";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import CreateQuestion from "./CreateQuestion";
import CreateMCQ from "./CreateMCQ";
import Verbal from "../../assets/images/Assessment/Verbal.svg";
import MCQ from "../../assets/images/Assessment/MCQ.svg";
import Coding from "../../assets/images/Assessment/Coding.svg";
import DeleteIcon from "@material-ui/icons/Delete";
import CreateIcon from "@mui/icons-material/Create";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import * as AssessmentActions from "../../store/Assessment/AssessmentAction";
import {
  Accordion,
  AccordionItem,
  AccordionItemHeading,
  AccordionItemButton,
  AccordionItemPanel,
} from "react-accessible-accordion";
import ListOfQuestions from "./ListOfQuestions";

const CollapsibleSettings = (props) => {
  const [expanded, setExpanded] = React.useState(false);

  useEffect(() => {
    if (props.isEdit || props.isEditAll) {
      setExpanded(true);
    }
  }, []);
  return (
    <Accordion>
      <AccordionItem>
        <AccordionItemHeading className="accordion-header">
          <AccordionItemButton className="item-button">
            <div className="first-flex-accordion">
              <div className="first-icon">
                {props.accordionHeading == "Verbal Questions" ? (
                  <img src={Verbal} />
                ) : props.accordionHeading == "MCQ" ? (
                  <img src={MCQ} />
                )  : props.accordionHeading == "Coding Skills" ? (
                  <img src={Coding} />) : null}
              </div>
              <div className="accordion-heading">{props.accordionHeading}</div>
              <div className="check-icon">
                {props.noOfQuestions && props.totalTime && !props.noShow ? (
                  <CheckIcon
                    style={{ height: "20px", width: "20px", color: "#04724d" }}
                  />
                ) : null}
              </div>
            </div>
            <div className="second-flex-accordion">
              {props.noOfQuestions ? (
                <div className="no-of-question">
                  No of Questions: {props.noOfQuestions}
                </div>
              ) : null}
              {props.totalTime ? (
                <div className="total-time">Total Time: {props.totalTime}</div>
              ) : null}
              {props.noShow ? null : (
                <div
                  className="collapsible-icon-div"
                  onClick={() => {
                    setExpanded(!expanded);
                  }}
                >
                  {expanded ? (
                    <KeyboardArrowUpIcon
                      style={{ color: "#fff", height: "25px", width: "25px" }}
                    />
                  ) : (
                    <KeyboardArrowDownIcon
                      style={{ color: "#fff", height: "25px", width: "25px" }}
                    />
                  )}
                </div>
              )}

              {props.edit && props.delete && !props.isViewOnly ? (
                <div className="edit-delete">
                  <div className="icon-edit-delete" onClick={()=>props.editHandlerNew()}>
                    <CreateIcon
                      style={{
                        height: "10px",
                        width: "10px",
                        color: "#1473e6",
                      }}
                    />
                  </div>
                  <div
                    className="icon-edit-delete"
                    onClick={props.deleteHandlerNew}
                  >
                    <DeleteIcon
                      style={{ height: "10px", width: "10px", color: "red" }}
                    />
                  </div>
                </div>
              ) : props.edit  && !props.isViewOnly  ?  <div className="edit-delete">
              <div className="icon-edit-delete" onClick={()=>props.editHandlerNew()}>
                <CreateIcon
                  style={{
                    height: "10px",
                    width: "10px",
                    color: "#1473e6",
                  }}
                />
              </div> </div>  : null
              }
            </div>
          </AccordionItemButton>
        </AccordionItemHeading>

        {expanded && !props.noShow ? (
          <div className="accordion-panel">
            {props.accordionHeading == "Verbal Questions" ? (
              <div>
                <CreateQuestion
                  skillData={[]}
                  testId={props.testId}
                  verbalQuestions={props.verbalQuestions}
                  rowData={props.rowData}
                  isEdit={props.isEdit }
                  isEditAll={props.isEditAll}
                  isViewOnly={props.isViewOnly}
                />

                {props.verbalQuestionsNew.length ? (
                  <div style={{ marginTop: "10px" }}>
                    {" "}
                    <ListOfQuestions
                      questions={props.verbalQuestionsNew}
                      editHandler={props.editHandler}
                      deleteHandler={props.deleteHandler}
                    />
                  </div>
                ) : null}
              </div>
            ) : null}
          </div>
        ) : props.noShow ? (
          props.itemPanel
        ) : null}
      </AccordionItem>
    </Accordion>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    AssessmentAction: bindActionCreators(AssessmentActions, dispatch),
  };
};
const mapStateToProps = (state) => {
  return {
    AssessmentReducer: state.AssessmentReducer,
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CollapsibleSettings);
