import React from "react";
import './tableRow.scss';
import SelectInput from '../Atoms/SelectInput/SelectInput';
import { TextField } from "@material-ui/core";
import ControlledCheckbox from "../Atoms/Checkbox";






export default function TableRowNew(props) {
 
    return <div className="main-table-row">
    <div className="question-type">
        {props.questionType}
    </div>
    <div className="no-of-questions">
         0{props.questionNumbers}
    </div>
    <div className={props.retake ? "last-column-retake" : "last-column"}>
   {
       props.weightage ? 
      <div style={{display:'flex',justifyContent:'center',alignItems:'center'}}> 
          <div>{props.value} </div>
          <div style={{color:'#333',marginLeft:'5px',fontSize:'13px' }}>% </div>
      </div> :
     props.skip ?      <ControlledCheckbox checked={props.checked}
     size={20}
     disabled
     borderRadius={2}
     borderColor={"#04724d"}
      label={'Skip once done'} handleChange={props.handleChange} /> : props.retake ?  <div className="retake-div">
  
        {
                  props.checked ?  <div className="retake-select"> 
                   <div> {props.retakeValue} </div>
                  </div> :         <ControlledCheckbox checked={props.checked}
      disabled
      size={20}
      borderRadius={2}
      borderColor={"#04724d"}
       label={''} handleChange={props.handleChange} /> 
              }
           </div>
      
      :  <div> 0{props.value} </div>
    
   }
    </div>

    </div>


}