import React from "react";
import './tableRow.scss';
import SelectInput from '../Atoms/SelectInput/SelectInput';
import { TextField } from "@material-ui/core";
import ControlledCheckbox from "../Atoms/Checkbox";
import { retakeOptions } from "./Assessment.config";






export default function TableRow(props) {
    let optionsdata=props.tableLength==1 ? [{value:1,label:1}] : props.tableLength==2 ? [{value:1,label:1},{value:2,label:2}] :  [{value:1,label:1},{value:2,label:2},{value:3,label:3}] 
    return <div className="main-table-row">
    <div className="question-type">
        {props.questionType}
    </div>
    <div className="no-of-questions">
         0{props.questionNumbers}
    </div>
    <div className={props.retake ? "last-column-retake" : "last-column"}>
   {
       props.weightage ? 
      <div style={{display:'flex',justifyContent:'center',alignItems:'center'}}> 
           <TextField id="standard-basic"  variant="outlined"
       placeholder=""
       error={props.totalWeightage!=100 || props.error}
         className={props.totalWeightage !=100 || props.error ? "weightage-input-error" : "weightage-input" } value={props.value} onChange={props.handleChange}/>
          <div style={{color:props.totalWeightage !=100 || props.error  ? '#FF4646' : '#333',marginLeft:'5px',fontSize:'13px' }}>% </div>
      </div> :
     props.skip ?      <ControlledCheckbox checked={props.checked}
     size={20}
     borderRadius={2}
     borderColor={"#04724d"}
      label={'Skip once done'} onChange={props.handleChange} /> : props.retake ?  
     <div className="retake-div">
          <ControlledCheckbox checked={props.checked}
      size={20}
      borderRadius={2}
      borderColor={"#04724d"}
       label={''} onChange={props.handleChange} /> 
        {
                  props.checked ?  <div className="retake-select"> 
                      <SelectInput
                  className="last-colum-select"
                  onChange={props.handleSelectRetake}
                  options={retakeOptions}
                  value={props.retakeValue}
                  placeholder={props.retakeValue ? props.retakeValue : ""}
                />
                  </div> : null
              }
          </div>
             
      
      :  <SelectInput
       className="last-colum-select"
       onChange={props.handleSelect}
       options={optionsdata}
       value={props.value}
       placeholder={props.value ? props.value : ""}
     />
    
   }
    </div>

    </div>


}