import React from "react";
import "./assessmentWeightageTable.scss";
import TableRow from "./TableRow";
import QuestionMarkIcon from "@mui/icons-material/QuestionMark";
import ErrorOutlineIcon from "@mui/icons-material/ErrorOutline";
import CheckIcon from "@mui/icons-material/Check";
import TableRowNew from "./TableRowNew";
import DeleteIcon from "@material-ui/icons/Delete";
import CreateIcon from "@mui/icons-material/Create";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import { styled } from "@mui/material/styles";
const LightTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(({}) => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: "#fff",
    color: "#000000",
    fontSize: 12,
    fontWeight: "600",
  },
}));
export default function AssessmentFlowTable(props) {
  let error = false;
  return (
    <div className="weight-main-assessment-table">
      {props.tableData.map((item) => {
        if (item.weightage == 0) {
          error = true;
          return;
        }
      })}
      <div
        className="weight-assessment-table-header"
        style={{
          padding:
            props.edit && props.delete
              ? "10px 20px 10px 40px"
              : "10px 80px 10px 40px",
        }}
      >
        {props.headerData2.map((item) => {
          return (
            <div className={"weight-item-header"}>
              {item != "Weightage" ? (
                item
              ) : (
                <div
                  style={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                  }}
                  className={
                    props.edit && props.delete ? "weight-item-header-new" : ""
                  }
                >
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                  >
                    <div>{item}</div>
                    <div className="weight-icon-div">
                      <LightTooltip
                        title="% will decide the weightage of type of question in the assignment"
                        placement="top"
                      >
                        <QuestionMarkIcon
                          style={{
                            height: "10px",
                            width: "10px",
                            color: "#fff",
                          }}
                        />
                      </LightTooltip>
                    </div>
                  </div>

                  {props.edit && props.delete && !props.isViewOnly ? (
                    <div className="edit-delete-new2">
                      <div
                        className="icon-edit-delete"
                        onClick={props.editHandler}
                      >
                        <CreateIcon
                          style={{
                            height: "10px",
                            width: "10px",
                            color: "#1473e6",
                          }}
                        />
                      </div>
                      {/* <div
                        className="icon-edit-delete"
                        onClick={props.deleteHandler}
                      >
                        <DeleteIcon
                          style={{
                            height: "10px",
                            width: "10px",
                            color: "red",
                          }}
                        />
                      </div> */}
                    </div>
                  ) : null}
                </div>
              )}
            </div>
          );
        })}
      </div>
      <div className="weight-table-rows">
        {props.edit && props.delete
          ? props.tableData.map((item) => {
              return (
                <TableRowNew
                  questionType={item.questionType}
                  error={error}
                  weightage={true}
                  totalWeightage={props.totalWeightage}
                  questionNumbers={item.questionNumbers}
                  value={item.weightage}
                  handleChange={(e) => {
                    props.handleWeightageChange(item, e);
                  }}
                />
              );
            })
          : props.tableData.map((item) => {
              return (
                <TableRow
                  questionType={item.questionType}
                  error={error}
                  weightage={true}
                  totalWeightage={props.totalWeightage}
                  questionNumbers={item.questionNumbers}
                  value={item.weightage}
                  handleChange={(e) => {
                    props.handleWeightageChange(item, e);
                  }}
                />
              );
            })}
      </div>

      <div className="weight-total-score">
        {props.delete && props.edit ? null : (
          <div className="weight-icon">
            <LightTooltip
              title={
                props.totalWeightage != 100 || error
                  ? "Please make sure total score remains 100%"
                  : ""
              }
              placement="top"
            >
              {props.totalWeightage != 100 || error ? (
                <ErrorOutlineIcon style={{ color: "#FF4646" }} />
              ) : (
                <CheckIcon style={{ color: "#21745D" }} />
              )}
            </LightTooltip>
          </div>
        )}
        <div
          className="weight-weight-card2"
          style={{
            color:
              props.delete && props.edit
                ? "#333"
                : props.totalWeightage != 100 || error
                ? "#FF4646"
                : "#21745D",
          }}
        >
          Total Score
        </div>
        <div className="weight-select">
          <div
            className={
              props.delete && props.edit
                ? "weight-score-edit-delete"
                : "weight-score"
            }
            style={{
              color:
                props.delete && props.edit
                  ? "#333"
                  : props.totalWeightage != 100 || error
                  ? "#FF4646"
                  : "#21745D",
              borderColor:
                props.totalWeightage != 100 || error ? "#FF4646" : "#21745D",
            }}
          >
            {props.totalWeightage}
          </div>
          <div
            style={{
              color:
                props.delete && props.edit
                  ? "#333"
                  : props.totalWeightage != 100 || error
                  ? "#FF4646"
                  : "#333",
              marginLeft: "5px",
            }}
          >
            %
          </div>
        </div>
      </div>
    </div>
  );
}
