import React from "react";
import "./scoringCardMCQ.scss";
import SelectInput from "../Atoms/SelectInput/SelectInput";
import QuestionMarkIcon from "@mui/icons-material/QuestionMark";
import McqQuestionCard from "./McqQuestionCard";
import { scoreOptions } from "./Assessment.config";
import { secondsToMinutes } from "../../utils/utility";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import { styled } from "@mui/material/styles";
const LightTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(() => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: "#fff",
    color: "#000000",
    fontSize: 12,
    fontWeight: "600",
  },
}));
export default function ScoringCardMCQ(props) {
  let optionsdata = scoreOptions;
  let totalScore = 0;
  return (
    <div className="mcq-score-body">
      <div className="mcq-header">
        <div className="mcq-questions">Questions</div>
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <div className="mcq-score">Select Score</div>
          <div className="icon-div">
            <LightTooltip
              title="1 means,1 mark for a question,2 means,2 mark for a question"
              placement="top"
            >
              <QuestionMarkIcon
                style={{ height: "10px", width: "10px", color: "#fff" }}
              />
            </LightTooltip>
          </div>
        </div>
      </div>
      <div className="mcq-body">
        {props.mcqQuestions.map((item) => {
          totalScore = totalScore + item.score;
          return (
            <div className="mcq-row-wrapper">
              <div className="mcq-mcq-card">
                <McqQuestionCard
                  mcqText={item.questionText}
                  time={secondsToMinutes(item.quesSec)}
                  mcqOptions={item.mcqOptions}
                  publish={true}
                  isViewOnly={props.isViewOnly}
                  skills={item.skills}
                />{" "}
              </div>
              <div className="mcq-select">
                {props.edit && props.delete ? (
                  <div style={{ fontWeight: "700" }}>0{item.score}</div>
                ) : (
                  <SelectInput
                    className="mcq-colum-select"
                    onChange={(e) => {
                      props.handleSelect(e, item);
                    }}
                    options={optionsdata}
                    value={item.score}
                    placeholder={item.score ? item.score : ""}
                  />
                )}{" "}
              </div>
            </div>
          );
        })}

        <div className="mcq-total-score">
          <div className="mcq-mcq-card2">Total Score</div>
          <div className="mcq-select">
            {props.edit && props.delete ? (
              <div div style={{ fontWeight: "700" }}>
                {totalScore < 10 ? `0${totalScore}` : totalScore}
              </div>
            ) : (
              <div className="mcq-score">
                {" "}
                {totalScore < 10 ? `0${totalScore}` : totalScore}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
