import React from "react";
import "./assessmentSkipTable.scss";
import TableRow from "./TableRow";
import QuestionMarkIcon from "@mui/icons-material/QuestionMark";
import DeleteIcon from "@material-ui/icons/Delete";
import CreateIcon from "@mui/icons-material/Create";
import TableRowNew from "./TableRowNew";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import { styled } from "@mui/material/styles";
const LightTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(() => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: "#fff",
    color: "#000000",
    fontSize: 12,
    fontWeight: "600",
  },
}));
export default function AssessmentFlowTable(props) {
  return (
    <div className="skip-main-assessment-table">
      <div
        className="skip-assessment-table-header"
        style={{
          padding:
            props.edit && props.delete
              ? "10px 20px 10px 40px"
              : "10px 80px 10px 40px",
        }}
      >
        {props.headerData3.map((item) => {
          return (
            <div className={"skip-item-header"}>
              {item != "Skip" ? (
                item
              ) : (
                <div
                  style={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                  }}
                  className={
                    props.edit && props.delete ? "skip-item-header-new" : ""
                  }
                >
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                  >
                    <div>{item}</div>
                    <div className="skip-icon-div">
                      <LightTooltip
                        title="Skip will decide type of question to be skipped in the assignment"
                        placement="top"
                      >
                        <QuestionMarkIcon
                          style={{
                            height: "10px",
                            width: "10px",
                            color: "#fff",
                          }}
                        />
                      </LightTooltip>
                    </div>
                  </div>

                  {props.edit && props.delete && !props.isViewOnly ? (
                    <div className="edit-delete-new3">
                      <div
                        className="icon-edit-delete"
                        onClick={props.editHandler}
                      >
                        <CreateIcon
                          style={{
                            height: "10px",
                            width: "10px",
                            color: "#1473e6",
                          }}
                        />
                      </div>
                      {/* <div
                        className="icon-edit-delete"
                        onClick={props.deleteHandler}
                      >
                        <DeleteIcon
                          style={{
                            height: "10px",
                            width: "10px",
                            color: "red",
                          }}
                        />
                      </div> */}
                    </div>
                  ) : null}
                </div>
              )}
            </div>
          );
        })}
      </div>
      <div className="skip-table-rows">
        {props.edit && props.delete
          ? props.tableData.map((item) => {
              return (
                <TableRowNew
                  questionType={item.questionType}
                  skip={true}
                  questionNumbers={item.questionNumbers}
                  checked={item.skip}
                  handleChange={(e) => {
                    props.handleSkipChange(item, e);
                  }}
                />
              );
            })
          : props.tableData.map((item) => {
              return (
                <TableRow
                  questionType={item.questionType}
                  skip={true}
                  questionNumbers={item.questionNumbers}
                  checked={item.skip}
                  handleChange={(e) => {
                    props.handleSkipChange(item, e);
                  }}
                />
              );
            })}
      </div>
    </div>
  );
}
