import React from "react";
import './listOfQuestions.scss';
import ListCardVerbal from "./ListCardVerbal";
import { secondsToMinutes } from "../../utils/utility";


export default function ListOfQuestions(props) {

    return <div className="list-card-main">
        <div className="list-card-heading">List Of Questions</div>
      
      
        {
         props.questions && props.questions.map(item=>{
       if(item.skills && item.quesSec){
        return <ListCardVerbal skills={item.skills} verbalText={item.questionText}
        deleteHandler={()=>{props.deleteHandler(item)}}
        editHandler={()=>{props.editHandler(item)}}
        time={`${secondsToMinutes(item.quesSec)}` }
        isPublished={item.isPublished}
        />
       }
         })
        }

    </div>

}