import React from "react";
import "./publish.scss";
import CollapsibleSettings from "../../../components/AssessmentComponents/CollapsibleSettings";
import VerbalQuestionCard from "../../../components/AssessmentComponents/VerbalQuestionCard";
import McqQuestionCard from "../../../components/AssessmentComponents/McqQuestionCard";
import AssessmentFlowTable from "../../../components/AssessmentComponents/AssessmentFlowTable";
import ScoringCardVerbal from "../../../components/AssessmentComponents/ScoringCardVerbal";
import ScoringCardMCQ from "../../../components/AssessmentComponents/ScoringCardMCQ";
import AssessmentWeightageTable from "../../../components/AssessmentComponents/AssessmentWeightageTable";
import RetakeSectionTable from "../../../components/AssessmentComponents/RetakeSectionTable";
import AssessmentSkipTable from "../../../components/AssessmentComponents/AssessmentSkipTable";
import Button from "../../../components/Controls/Button/Button";
import MainSteps from "../MainSteps/MainSteps";
// import LoadingBar from 'react-top-loading-bar';
import { withRouter } from "react-router-dom";
import * as AssessmentAction from "../../../store/Assessment/AssessmentAction";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { secondsToHours } from "../../../utils/utility";
import { secondsToMinutes } from "../../../utils/utility";
import ControlledDialog from "../../../components/Atoms/Dialog";
import DeleteIcon from "@material-ui/icons/Delete";
import ReactGA from "react-ga";

class Publish extends React.Component {
  state = {
    testId: "",
    suggestedDurationVerbal: "",
    suggestedDurationMcq: "",
    suggestedDurationCoding: "",
    verbalQuestions: [],
    mcqQuestions: [],
    codingQuestions: [],
    assessmentTableData: [],
    currentTest: [],
    progress: 0,
    stop: false,
    isViewOnly: false,
    showVerbalDeleteDialog: false,
    showWarningVerbalDialog: false,
    showMcqDeleteDialog: false,
    showWarningMcqDialog: false,
    showCodingDeleteDialog: false,
    showWarningCodingDialog: false,
    jdName: "",
    jdId: "",
    assessmentName: "",
    assessmentId: "",
    publish: false,
  };

  componentDidMount() {
    ReactGA.pageview(
      `${
        window.location.pathname.split("/assessment/step2/publish/")[0]
      }/assessment/step2/publish/`
    );
    setInterval(() => this.handleProgressChange(), 100);
    if (
      this.props &&
      this.props.location &&
      this.props.location.state &&
      this.props.location.state.testId
    ) {
      this.setState({
        testId: this.props.location.state.testId,
      });
      this.props.AssessmentAction.getTestById({
        id: this.props.location.state.testId,
      });
    } else {
      if (window.location.pathname.includes("assessment/step2/publish/")) {
        let testId = window.location.pathname.split(
          "assessment/step2/publish/"
        )[1];
        if (testId) {
          this.setState({
            testId: testId,
          });
          this.props.AssessmentAction.getTestById({
            id: testId,
          });
        }
      }
    }
    if (
      this.props &&
      this.props.location &&
      this.props.location.state &&
      this.props.location.state.isFromList
    ) {
      this.setState({
        isViewOnly: this.props.location.state.isFromList,
      });
    }
  }
  handleProgressChange = () => {
    if (this.state.progress < 90 && !this.state.stop) {
      this.setState({
        progress: this.state.progress + 15,
      });
    }
  };

  componentDidUpdate(prevProps) {
    const prev = prevProps && prevProps.AssessmentReducer;
    const cur = this.props && this.props.AssessmentReducer;

    // if (prev.isTestUpdated !== cur.isTestUpdated ) {
    //      this.props.history.push({
    //   pathname:'/schedule',
    //   state:{
    //     assessmentName:this.state.assessmentName,
    //     assessmentId:this.state.assessmentId,
    //     jdName:this.state.jdName,
    //     jdId:this.state.jdId
    //   }
    // })

    //   }

    if (prev.isGetTestById !== cur.isGetTestById) {
      if (cur.testById && cur.testById.data) {
        let questionType = [];
        this.setState({
          currentTest: cur.testById.data,
          assessmentName: cur.testById.data && cur.testById.data.name,
          assessmentId: cur.testById.data && cur.testById.data.id,
          jdName:
            cur.testById.data &&
            cur.testById.data.jdId &&
            cur.testById.data.jdId.title,
          jdId:
            cur.testById.data &&
            cur.testById.data.jdId &&
            cur.testById.data.jdId.id,
        });
        cur.testById.data.questionType.map((item) => {
          questionType.push(item.id.name.toLowerCase());
        });

        this.setState({
          questionType: questionType,
        });
      }

      let verbalQuestions = [];
      let suggestedDurationVerbal = 0;
      let suggestedDurationMcq = 0;
      let suggestedDurationCoding = 0;
      cur.testById.data.verbalQuestions.map((item) => {
        suggestedDurationVerbal += item && item.id && item.id.suggestedDuration;
        verbalQuestions.push({
          id: item.id && item.id.id,
          questionText: item.id && item.id.text,
          skills: item.id && item.id.skills,
          quesMins: 0,
          quesSec: item.id && item.id.suggestedDuration,
          score: item.score,
        });
      });

      let mcqQuestions = [];
      cur.testById.data.mcqQuestions.map((item) => {
        suggestedDurationMcq += item.id && item.id.suggestedDuration;
        mcqQuestions.push({
          id: item.id && item.id.id,
          questionText: item.id && item.id.text,
          skills: item.id && item.id.skills,
          quesMins: 0,
          quesSec: item.id && item.id.suggestedDuration,
          mcqOptions: item.id && item.id.metadata && item.id.metadata.option,
          score: item.score,
        });
      });

      let codingQuestions = [];
      cur.testById.data.codingQuestions.map((item) => {
        suggestedDurationCoding += item.id && item.id.suggestedDuration;
        codingQuestions.push({
          id: item.id && item.id.id,
          questionText: item.id && item.id.text,
          skills: item.id && item.id.skills,
          quesMins: 0,
          quesSec: item.id && item.id.suggestedDuration,
          score: item.score,
          allowedLanguages:
            item.id && item.id.metadata && item.id.metadata.allowedLanguages,
          autocomplete:
            item.id && item.id.metadata && item.id.metadata.autocomplete,
        });
      });

      this.setState({
        verbalQuestions: verbalQuestions,
        mcqQuestions: mcqQuestions,
        suggestedDurationVerbal: suggestedDurationVerbal,
        suggestedDurationMcq: suggestedDurationMcq,
        codingQuestions: codingQuestions,
        suggestedDurationCoding: suggestedDurationCoding,
      });

      let assessmentTableData = [];
      cur.testById.data.questionType.map((item, index) => {
        assessmentTableData.push({
          id: item.id.id,
          questionType: `${item.id.name}`,
          questionNumbers:
            item.id.name.toLowerCase() == "verbal"
              ? cur.testById.data.verbalQuestions.length
              : item.id.name.toLowerCase() == "mcq"
              ? cur.testById.data.mcqQuestions.length
              : item.id.name.toLowerCase() == "coding"
              ? cur.testById.data.codingQuestions.length
              : 0,
          flow: index + 1,
          weightage: item.weightage,
          skip: item.skipable,
          retake: item.retake ? true : false,
          retakeValue: item.retake,
        });
      });

      this.setState({
        assessmentTableData: assessmentTableData,
      });

      this.setState({
        progress: 100,
      });

      setTimeout(() => {
        this.setState({ stop: true });
      }, 1000);
    }

    if (
      prev.isTestUpdated !== cur.isTestUpdated &&
      cur.testById &&
      cur.testById.data &&
      !this.state.publish
    ) {
      this.props.AssessmentAction.getTestById({ id: this.state.testId });
      this.setState({
        showVerbalDeleteDialog: false,
        showMcqDeleteDialog: false,
        showCodingDeleteDialog: false,
      });

      this.props.history.push({
        pathname: `/assessment/step2/assignment-flow/${this.state.testId}`,
        state: {
          testId: this.state.testId,
          fromPublish: true,
        },
      });
    }

    if (
      prev.isTestUpdated !== cur.isTestUpdated &&
      cur.testById &&
      cur.testById.data &&
      this.state.publish
    ) {
      // this.props.history.push({
      //   pathname: "/schedule",
      //   state: {
      //     assessmentName: this.state.assessmentName,
      //     assessmentId: this.state.assessmentId,
      //     jdName: this.state.jdName,
      //     jdId: this.state.jdId,
      //   },
      // });

      this.props.history.push({
        pathname: "/assessmentList",
        state: {
          assessmentName: this.state.assessmentName,
          assessmentId: this.state.assessmentId,
          jdName: this.state.jdName,
          jdId: this.state.jdId,
        },
      });
    }
  }

  deleteHandlerMcq = () => {
    if (
      this.state.verbalQuestions.length ||
      this.state.codingQuestions.length
    ) {
      this.setState({
        showMcqDeleteDialog: true,
      });
    } else {
      this.setState({
        showWarningMcqDialog: true,
      });
    }
  };
  deleteHandlerVerbal = () => {
    if (this.state.mcqQuestions.length || this.state.codingQuestions.length) {
      this.setState({
        showVerbalDeleteDialog: true,
      });
    } else {
      this.setState({
        showWarningVerbalDialog: true,
      });
    }
  };

  deleteHandlerCoding = () => {
    if (this.state.mcqQuestions.length || this.state.verbalQuestions.length) {
      this.setState({
        showCodingDeleteDialog: true,
      });
    } else {
      this.setState({
        showWarningCodingDialog: true,
      });
    }
  };
  editHandler = () => {
    this.props.history.push({
      pathname: `/assessment/step2/create-assignment/${this.state.testId}`,
      state: {
        testId: this.state.testId,
      },
    });
  };

  commonEditHandler = () => {
    this.props.history.push({
      pathname: `/assessment/step2/assignment-flow/${this.state.testId}`,
      state: {
        testId: this.state.testId,
      },
    });
  };

  deleteAllVerbalQuestions = () => {
    const body = {
      verbalQuestions: [],
    };

    this.props.AssessmentAction.updateTest({
      body: body,
      id: this.state.testId,
    });
  };
  deleteAllMcqQuestions = () => {
    const body = {
      mcqQuestions: [],
    };

    this.props.AssessmentAction.updateTest({
      body: body,
      id: this.state.testId,
    });
  };

  deleteAllCodingQuestions = () => {
    const body = {
      codingQuestions: [],
    };

    this.props.AssessmentAction.updateTest({
      body: body,
      id: this.state.testId,
    });
  };

  saveContinueHandler = async () => {
    await this.setState({
      publish: true,
    });

    this.props.AssessmentAction.updateTest({
      body: {
        creationComplete: true,
      },
      id: this.state.testId,
    });
  };

  render() {
    let totalWeightage = 0;
    this.state.assessmentTableData.map((item) => {
      if (item.weightage != "") {
        totalWeightage = totalWeightage + parseInt(item.weightage);
      }
    });
    const itemPanel = (
      <div className='publish-verbal-questions-card'>
        {this.state.verbalQuestions.map((item) => {
          return (
            <div className='publish-card-class'>
              {" "}
              <VerbalQuestionCard
                verbalText={item.questionText}
                time={secondsToMinutes(item.quesSec)}
                publish={true}
                isViewOnly={this.state.isViewOnly}
              />{" "}
            </div>
          );
        })}
      </div>
    );

    const itemPanel2 = (
      <div className='publish-verbal-questions-card'>
        {this.state.mcqQuestions.map((item) => {
          return (
            <div className='publish-card-class'>
              <McqQuestionCard
                mcqText={item.questionText}
                time={secondsToMinutes(item.quesSec)}
                selected={item.selected}
                mcqOptions={item.mcqOptions}
                publish={true}
                isViewOnly={true}
              />
            </div>
          );
        })}
      </div>
    );
    const itemPanel3 = (
      <div className='publish-verbal-questions-card'>
        {this.state.codingQuestions.map((item) => {
          return (
            <div className='publish-card-class'>
              <McqQuestionCard
                mcqText={item.questionText}
                time={secondsToMinutes(item.quesSec)}
                selected={item.selected}
                publish={true}
                isViewOnly={true}
              />
            </div>
          );
        })}
      </div>
    );

    let scoringTable = (
      <div className='publish-score-card'>
        <ScoringCardVerbal
          verbalQuestions={this.state.verbalQuestions}
          edit={true}
          delete={true}
          isViewOnly={this.state.isViewOnly}
        />
      </div>
    );

    const scoringTableMCQ = (
      <div className='publish-score-card'>
        <ScoringCardMCQ
          mcqQuestions={this.state.mcqQuestions}
          edit={true}
          delete={true}
          isViewOnly={this.state.isViewOnly}
        />
      </div>
    );

    const scoringTableCoding = (
      <div className='publish-score-card'>
        <ScoringCardMCQ
          mcqQuestions={this.state.codingQuestions}
          edit={true}
          delete={true}
          isViewOnly={this.state.isViewOnly}
        />
      </div>
    );
    return (
      <div className='main-preview-wrapper-steps'>
        <div>
          <ControlledDialog
            showDialog={this.state.showVerbalDeleteDialog}
            handleCloseDialog={() => {
              this.setState({
                showVerbalDeleteDialog: false,
              });
            }}
            dialogClassName={"deleteDialog"}
            dialogAction1Text='Cancel'
            dialogAction2Text='Confirm'
            showClose={true}
            iconTop={
              <DeleteIcon
                style={{ height: "15px", width: "15px", color: "red" }}
              />
            }
            headingTop='Delete'
            iconTopClass='icon-top'
            headingTopClass='heading-top'
            dialogAction1Class='dialog-action-1-class'
            dialogAction2Class='dialog-action-2-class'
            handleAction2={() => {
              this.deleteAllVerbalQuestions();
            }}
            handleAction1={() => {
              this.setState({
                showVerbalDeleteDialog: false,
              });
            }}
          >
            <div className='dialog-main-heading'>
              Are you sure you want to delete Entire Verbal Questions?
            </div>
            <div className='dialog-sub-heading'>
              Click to confirm to delete verbal questions from this assessment
              permanently
            </div>
          </ControlledDialog>
        </div>

        <div>
          <ControlledDialog
            showDialog={this.state.showWarningVerbalDialog}
            handleCloseDialog={() => {
              this.setState({
                showWarningVerbalDialog: false,
              });
            }}
            dialogClassName={"deleteDialog"}
            dialogAction1Text='Cancel'
            showClose={true}
            iconTop={
              <DeleteIcon
                style={{ height: "15px", width: "15px", color: "red" }}
              />
            }
            headingTop='Delete'
            iconTopClass='icon-top'
            headingTopClass='heading-top'
            dialogAction1Class='dialog-action-1-class'
            dialogAction2Class='dialog-action-2-class'
            handleAction1={() => {
              this.setState({
                showWarningVerbalDialog: false,
              });
            }}
          >
            <div className='dialog-main-heading'>
              Verbal Questions Can't be deleted since there are no mcq questions
              or coding questions
            </div>
          </ControlledDialog>
        </div>

        <div>
          <ControlledDialog
            showDialog={this.state.showMcqDeleteDialog}
            handleCloseDialog={() => {
              this.setState({
                showMcqDeleteDialog: false,
              });
            }}
            dialogClassName={"deleteDialog"}
            dialogAction1Text='Cancel'
            dialogAction2Text='Confirm'
            showClose={true}
            iconTop={
              <DeleteIcon
                style={{ height: "15px", width: "15px", color: "red" }}
              />
            }
            headingTop='Delete'
            iconTopClass='icon-top'
            headingTopClass='heading-top'
            dialogAction1Class='dialog-action-1-class'
            dialogAction2Class='dialog-action-2-class'
            handleAction2={() => {
              this.deleteAllMcqQuestions();
            }}
            handleAction1={() => {
              this.setState({
                showMcqDeleteDialog: false,
              });
            }}
          >
            <div className='dialog-main-heading'>
              Are you sure you want to delete Entire Mcq Questions?
            </div>
            <div className='dialog-sub-heading'>
              Click to confirm to delete mcq questions from this assessment
              permanently
            </div>
          </ControlledDialog>
        </div>

        <div>
          <ControlledDialog
            showDialog={this.state.showWarningMcqDialog}
            handleCloseDialog={() => {
              this.setState({
                showWarningMcqDialog: false,
              });
            }}
            dialogClassName={"deleteDialog"}
            dialogAction1Text='Cancel'
            showClose={true}
            iconTop={
              <DeleteIcon
                style={{ height: "15px", width: "15px", color: "red" }}
              />
            }
            headingTop='Delete'
            iconTopClass='icon-top'
            headingTopClass='heading-top'
            dialogAction1Class='dialog-action-1-class'
            dialogAction2Class='dialog-action-2-class'
            handleAction1={() => {
              this.setState({
                showWarningMcqDialog: false,
              });
            }}
          >
            <div className='dialog-main-heading'>
              Mcq Questions Can't be deleted since there are no verbal questions
              or coding questions
            </div>
          </ControlledDialog>
        </div>

        <div>
          <ControlledDialog
            showDialog={this.state.showCodingDeleteDialog}
            handleCloseDialog={() => {
              this.setState({
                showCodingDeleteDialog: false,
              });
            }}
            dialogClassName={"deleteDialog"}
            dialogAction1Text='Cancel'
            dialogAction2Text='Confirm'
            showClose={true}
            iconTop={
              <DeleteIcon
                style={{ height: "15px", width: "15px", color: "red" }}
              />
            }
            headingTop='Delete'
            iconTopClass='icon-top'
            headingTopClass='heading-top'
            dialogAction1Class='dialog-action-1-class'
            dialogAction2Class='dialog-action-2-class'
            handleAction2={() => {
              this.deleteAllCodingQuestions();
            }}
            handleAction1={() => {
              this.setState({
                showCodingDeleteDialog: false,
              });
            }}
          >
            <div className='dialog-main-heading'>
              Are you sure you want to delete Entire Coding Questions?
            </div>
            <div className='dialog-sub-heading'>
              Click to confirm to delete coding questions from this assessment
              permanently
            </div>
          </ControlledDialog>
        </div>

        <div>
          <ControlledDialog
            showDialog={this.state.showWarningCodingDialog}
            handleCloseDialog={() => {
              this.setState({
                showWarningCodingDialog: false,
              });
            }}
            dialogClassName={"deleteDialog"}
            dialogAction1Text='Cancel'
            showClose={true}
            iconTop={
              <DeleteIcon
                style={{ height: "15px", width: "15px", color: "red" }}
              />
            }
            headingTop='Delete'
            iconTopClass='icon-top'
            headingTopClass='heading-top'
            dialogAction1Class='dialog-action-1-class'
            dialogAction2Class='dialog-action-2-class'
            handleAction1={() => {
              this.setState({
                showWarningCodingDialog: false,
              });
            }}
          >
            <div className='dialog-main-heading'>
              Coding Questions Can't be deleted since there are no mcq questions
              or verbalQuestions
            </div>
          </ControlledDialog>
        </div>
        <MainSteps activeStep={3} />
        <div className='publish-summary-main'>
          <div className='publish-summary-heading'>Summary</div>

          {this.state.verbalQuestions.length ? (
            <div className='publish-verbal-summary'>
              <CollapsibleSettings
                accordionHeading='Verbal Questions'
                edit={true}
                delete={true}
                noOfQuestions={0}
                totalTime={secondsToHours(this.state.suggestedDurationVerbal)}
                noShow={true}
                itemPanel={itemPanel}
                isViewOnly={this.state.isViewOnly}
                deleteHandlerNew={this.deleteHandlerVerbal}
                editHandlerNew={this.editHandler}
              />
            </div>
          ) : null}

          {this.state.mcqQuestions.length ? (
            <div className='publish-mcq-summary'>
              <CollapsibleSettings
                accordionHeading='MCQ'
                edit={true}
                delete={true}
                noOfQuestions={0}
                totalTime={secondsToHours(this.state.suggestedDurationMcq)}
                noShow={true}
                itemPanel={itemPanel2}
                isViewOnly={this.state.isViewOnly}
                deleteHandlerNew={this.deleteHandlerMcq}
                editHandlerNew={this.editHandler}
              />
            </div>
          ) : null}

          {this.state.codingQuestions.length ? (
            <div className='publish-mcq-summary'>
              <CollapsibleSettings
                accordionHeading='Coding Skills'
                edit={true}
                delete={true}
                noOfQuestions={0}
                totalTime={secondsToHours(this.state.suggestedDurationCoding)}
                noShow={true}
                itemPanel={itemPanel3}
                isViewOnly={this.state.isViewOnly}
                deleteHandlerNew={this.deleteHandlerCoding}
                editHandlerNew={this.editHandler}
              />
            </div>
          ) : null}

          <div className='publish-assessment-flow'>
            <div className='publish-assessment-flow-heading'>
              Assessment Flow{" "}
            </div>

            <div className='publish-assessment-flow-table'>
              {" "}
              <AssessmentFlowTable
                edit={true}
                delete={true}
                handleFlowChange={this.handleFlowChange}
                headerData={[
                  "Type of Question",
                  "Number of Question",
                  "Flow Mapping",
                ]}
                isViewOnly={this.state.isViewOnly}
                tableData={this.state.assessmentTableData}
                editHandler={this.commonEditHandler}
              />
            </div>
          </div>

          <div className='publish-assessment-flow'>
            <div className='publish-assessment-flow-heading'>Scoring </div>

            {this.state.verbalQuestions.length ? (
              <div className='publish-scoring-summary'>
                <CollapsibleSettings
                  accordionHeading='Verbal Questions'
                  edit={true}
                  // delete={true}
                  noShow={true}
                  itemPanel={scoringTable}
                  isViewOnly={this.state.isViewOnly}
                  editHandlerNew={this.commonEditHandler}
                />
              </div>
            ) : null}

            {this.state.mcqQuestions.length ? (
              <div className='publish-scoring-summary'>
                <CollapsibleSettings
                  accordionHeading='MCQ'
                  edit={true}
                  // delete={true}
                  noShow={true}
                  itemPanel={scoringTableMCQ}
                  isViewOnly={this.state.isViewOnly}
                  editHandlerNew={this.commonEditHandler}
                />
              </div>
            ) : null}

            {this.state.codingQuestions.length ? (
              <div className='publish-scoring-summary'>
                <CollapsibleSettings
                  accordionHeading='Coding Skills'
                  edit={true}
                  // delete={true}
                  noShow={true}
                  itemPanel={scoringTableCoding}
                  isViewOnly={this.state.isViewOnly}
                  editHandlerNew={this.commonEditHandler}
                />
              </div>
            ) : null}
          </div>

          <div className='publish-assessment-flow'>
            <div className='publish-assessment-flow-heading'>
              Weightage Section{" "}
            </div>

            <div className='publish-assessment-flow-table'>
              {" "}
              <AssessmentWeightageTable
                edit={true}
                delete={true}
                handleWeightageChange={this.handleWeightageChange}
                headerData2={[
                  "Type of Question",
                  "Number of Question",
                  "Weightage",
                ]}
                tableData={this.state.assessmentTableData.filter(
                  (el) => el.questionType.toLowerCase() !== "coding"
                )}
                totalWeightage={totalWeightage}
                isViewOnly={this.state.isViewOnly}
                editHandler={this.commonEditHandler}
              />
            </div>
          </div>

          <div className='publish-assessment-flow'>
            <div className='publish-assessment-flow-heading'>
              Retake Section{" "}
            </div>

            {this.state.verbalQuestions.length ? (
              <div className='publish-assessment-flow-table'>
                {" "}
                <RetakeSectionTable
                  headerData3={[
                    "Type of Question",
                    "Number of Question",
                    "Retake",
                  ]}
                  tableData={this.state.assessmentTableData}
                  handleRetakeChange={this.handleRetakeChange}
                  edit={true}
                  delete={true}
                  isViewOnly={this.state.isViewOnly}
                  editHandler={this.commonEditHandler}
                />
              </div>
            ) : null}
          </div>

          <div className='publish-assessment-flow'>
            <div className='publish-assessment-flow-heading'>Skip Section </div>

            <div className='publish-assessment-flow-table'>
              <AssessmentSkipTable
                headerData3={["Type of Question", "Number of Question", "Skip"]}
                tableData={this.state.assessmentTableData}
                handleSkipChange={this.handleSkipChange}
                edit={true}
                delete={true}
                isViewOnly={this.state.isViewOnly}
                editHandler={this.commonEditHandler}
              />
            </div>
          </div>

          {this.state.isViewOnly ? null : (
            <div className='publish-save-continue-back-button-div'>
              <Button
                className='publish-save-continue-button'
                onClick={this.saveContinueHandler}
              >
                Finish
              </Button>

              <Button
                className='publish-save-back-button'
                onClick={this.commonEditHandler}
              >
                Go Back
              </Button>
            </div>
          )}
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    AssessmentReducer: state.AssessmentReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    AssessmentAction: bindActionCreators(AssessmentAction, dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Publish));
